
body,h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,fieldset,legend,button,input,textarea,th,td{margin:0;padding:0
}
body,input,textarea,select,button,table{border-collapse:collapse;border-spacing:0
}
body{font:normal 1em/1.25em 'microsoft Yahei',Verdana,Arial,Helvetica,sans-serif;color:#000;-webkit-text-size-adjust:none
}
h1,h2,h3,h4,h5,h6{font-size:100%
}
img,fieldset{border:0 none
}
ul,ol,li{list-style:none
}
em,address{font-style:normal
}
table{border-collapse:collapse
}
em,i{font-style:normal
}
strong,b{font-weight:normal
}
img{border:none
}
input,img{vertical-align:middle
}
input{outline:none
}
*{-webkit-tap-highlight-color:transparent
}
textarea:focus{outline:0
}
a{text-decoration:none
}
.clearfix:after{display:block;clear:both;visibility:hidden;height:0;content:" ";font-size:0
}
.clearfix{*zoom:1
}
.iui-button.is-disabled{opacity:.6
}
.iui-button{box-sizing:border-box;width:100%;height:41px;margin:10px 0;cursor:pointer;text-align:center;border-radius:4px;border:1px solid #bfcbd9;background-color:#fff;font-size:18px;overflow:hidden;-webkit-appearance:none
}
.iui-button:active:not(.is-disabled){color:#8492a6;background-color:#f9fafc
}
.iui-button:focus{outline:0
}
.iui-button_info{color:#fff;background-color:#20a0ff;border-color:#20a0ff
}
.iui-button_info:active:not(.is-disabled){background-color:#1d8ce0;color:#f9fafc
}
.iui-button_success{color:#fff;background-color:#13ce66;border-color:#13ce66
}
.iui-button_success:active:not(.is-disabled){background-color:#1dc668;color:#f9fafc
}
.iui-button_warning{color:#fff;background-color:#f7ba2a;border-color:#f7ba2a
}
.iui-button_warning:active:not(.is-disabled){background-color:#dea726;color:#f9fafc
}
.iui-button_danger{color:#fff;background-color:#ff4949;border-color:#ff4949
}
.iui-button_danger:active:not(.is-disabled){background-color:#ce3c39;color:#f9fafc
}



