
body,h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,fieldset,legend,button,input,textarea,th,td{margin:0;padding:0
}
body,input,textarea,select,button,table{border-collapse:collapse;border-spacing:0
}
body{font:normal 1em/1.25em 'microsoft Yahei',Verdana,Arial,Helvetica,sans-serif;color:#000;-webkit-text-size-adjust:none
}
h1,h2,h3,h4,h5,h6{font-size:100%
}
img,fieldset{border:0 none
}
ul,ol,li{list-style:none
}
em,address{font-style:normal
}
table{border-collapse:collapse
}
em,i{font-style:normal
}
strong,b{font-weight:normal
}
img{border:none
}
input,img{vertical-align:middle
}
input{outline:none
}
*{-webkit-tap-highlight-color:transparent
}
textarea:focus{outline:0
}
a{text-decoration:none
}
.clearfix:after{display:block;clear:both;visibility:hidden;height:0;content:" ";font-size:0
}
.clearfix{*zoom:1
}
.iui-dialog_mask{opacity:1;position:fixed;z-index:1000;top:0;right:0;left:0;bottom:0;background:rgba(0,0,0,0.6)
}
.iui-dialog_main{position:fixed;background-color:#fff;left:10%;top:35%;z-index:9999;width:80%;max-width:400px;text-align:center;border-radius:5px;overflow:hidden
}
.iui-dialog_hd{margin-top:15px;font-size:16px;font-weight:700;color:#324057;text-align:center
}
.iui-dialog_bd{padding:10px 20px 15px;border-bottom:1px solid #bfcbd9;min-height:36px;position:relative
}
.iui-dialog_buts{display:-ms-flexbox;display:flex;height:40px;line-height:40px
}
.iui-dialog_buts-cancel,.iui-dialog_buts-confirm{line-height:35px;display:block;background-color:#fff;-ms-flex:1;flex:1
}
.iui-dialog_buts-cancel{color:#475669;border-right:1px solid #bfcbd9
}
.iui-dialog_buts-confirm{color:#1d8ce0
}



