
body,h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,fieldset,legend,button,input,textarea,th,td{margin:0;padding:0
}
body,input,textarea,select,button,table{border-collapse:collapse;border-spacing:0
}
body{font:normal 1em/1.25em 'microsoft Yahei',Verdana,Arial,Helvetica,sans-serif;color:#000;-webkit-text-size-adjust:none
}
h1,h2,h3,h4,h5,h6{font-size:100%
}
img,fieldset{border:0 none
}
ul,ol,li{list-style:none
}
em,address{font-style:normal
}
table{border-collapse:collapse
}
em,i{font-style:normal
}
strong,b{font-weight:normal
}
img{border:none
}
input,img{vertical-align:middle
}
input{outline:none
}
*{-webkit-tap-highlight-color:transparent
}
textarea:focus{outline:0
}
a{text-decoration:none
}
.clearfix:after{display:block;clear:both;visibility:hidden;height:0;content:" ";font-size:0
}
.clearfix{*zoom:1
}
.iui-header{background-color:#20a0ff;height:40px;line-height:40px;text-align:center;display:-ms-flexbox;display:flex;color:#fff
}
.iui-header_title{-ms-flex:1;flex:1;font-weight:400;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.iui-header_left{-ms-flex:0.5;flex:0.5;text-align:left
}
.iui-header_right{-ms-flex:0.5;flex:0.5;text-align:right
}
.is-fixed{position:fixed 0 0;z-index:9999
}



